import React from 'react';
import { StarRating } from '../../util/StarRating';
import "./Business.css"


 
class Business extends React.Component {
    constructor(props){
        super(props);
        
        this.openMaps = this.openMaps.bind(this)
    }
    openMaps(query){
        window.open( 
            "https://www.google.com/maps/search/?api=1&query=" + query, "_blank");
    }
    openBusiness(url){
        window.open( 
            url);
    }
    render() {
        return (
            <div className="Business">
                <div className="image-container">
                    <img src={this.props.business.imageSrc} alt='' onClick={this.openBusiness.bind(this,this.props.business.url)} />
                </div>
                <h2>{this.props.business.name}</h2>
                <div className="Business-information">
                    <div className="Business-address" onClick={this.openMaps.bind(this,`${this.props.business.name}+${this.props.business.city}`)}>
                
                        <p>{this.props.business.address}</p>
                        <p>{this.props.business.city}</p>
                        <p>{this.props.business.state + " " +  this.props.business.zipCode}</p>
                    </div>
                    <div className="Business-reviews">
                        <h3>{this.props.business.category}</h3>
                        <StarRating rating={this.props.business.rating} />
                   
                        <p>{this.props.business.reviewCount} reviews</p>
                    </div>
                </div>
            </div>
        )
    }
}

export default Business;